/**axios
 * 
 */
import axios from 'axios';
import QS from 'qs';
import store from '../store/index';
import snackbar from '../components/Snackbar/int';
import router from '../router';

let requestor = axios.create();

// 
if (process.env.NODE_ENV == 'development') {
    // requestor.defaults.baseURL = 'https://homeapi.kilins.com/api';
    requestor.defaults.baseURL = 'http://10.0.0.123:3003/api';
} else if (process.env.NODE_ENV == 'debug') {
    requestor.defaults.baseURL = '';
} else if (process.env.NODE_ENV == 'production') {
    requestor.defaults.baseURL = 'https://homeapi.kilins.com/api';
}



// 
requestor.defaults.timeout = 10000;

// post
requestor.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
requestor.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';

// 
requestor.interceptors.request.use(
    config => {
        // tokenhttpheadertoken
        // tokentoken
        const token = "Bearer " + localStorage.getItem('token');
        (config.headers.Authorization = token);
        return config;
    },
    error => {
        return Promise.error(error);
    });

// 
requestor.interceptors.response.use(
    response => {
        if (response.data.code === 'offline') {
            this.$store.commit('UPDATE_LOADING', false);
            this.$store.commit('UPDATE_OfflineShow', true);
        } else {
            if (response.status === 200) {
                return Promise.resolve(response);
            } else {
                return Promise.reject(response);
            }
        }
    },
    // 200 
    error => {
        if (error.response.status) {
            switch (error.response.status) {
                // 401:     
                //     
                //     
                case 401:
                    snackbar.error('');
                    if (localStorage.getItem('token')) {
                        router.push({ path: '/Login' });
                        localStorage.removeItem('token');
                        store.commit('pushtoken', '');
                    }
                    router.push({ path: '/Login' });
                    break;
                // 403 token    
                //     
                // tokenvuextoken    
                //     
                case 403:
                    snackbar.error('');
                    // token     
                    localStorage.removeItem('token');
                    store.commit('pushtoken', '');
                    // fullPath
                    setTimeout(() => {
                        this.$router.replace({
                            path: '/Login',
                            query: {
                                redirect: this.$router.currentRoute.fullPath
                            }
                        });
                    }, 1000);
                    break;
                // 404    
                case 404:
                    snackbar.error('');
                    break;
                //     
                default:
                    snackbar.error(error.response.data.message);
            }
            return Promise.reject(error.response);
        }
    }
);
/** 
 * getget 
 * @param {String} url [url] 
 * @param {Object} params [] 
 */
export function get(url, params) {
    return new Promise((resolve, reject) => {
        requestor.get(url, {
            params: params
        })
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                reject(err.data);
            });
    });
}
/** 
 * postpost 
 * @param {String} url [url] 
 * @param {Object} params [] 
 */
export function post(url, params) {
    return new Promise((resolve, reject) => {
        requestor.post(url, QS.stringify(params))
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                reject(err.data);
            });
    });
}